* {
  margin: 0;
  padding: 0;
}
.ContentSection {
  margin: 25px;
  padding: 25px;
}

.navContainer {
  background: #af53d5;
  text-align: center;
  padding: 1rem 0;
}

.nav_ul li {
  padding: 0 0.6rem;
}

.nav_ul a {
  font-size: 1.05rem;
  color: #000 !important;
  font-weight: 400;
  letter-spacing: 0.5px;
}

.btn1 {
  border: 0;
  padding: 0.5rem 1.8rem;
  color: #fff;
  background-color: #cd5a5a;
}

.btn2 {
  border: 0;
  padding: 0.5rem 1.2rem;
  color: #fff;
  background-color: #72d187;
}
.btn3 {
  border-radius: 5px;
  padding: 0.5rem 1.8rem;
  color: #000;
  background-color: #d4aef6;
}
.Footer {
  padding: 0;
  margin-top: 8rem;
}

.ft-1 h3 {
  font-weight: 700;
  font-family: cursive;
  letter-spacing: 2px;
  padding: 0.5rem 0;
}

.ft-1 span {
  color: #a109c2;
}

.ft-1 p {
  padding: 0rem 5rem 0.8rem 0;
  font-weight: 500;
}

.footer-icons i {
  padding: 0.4rem 0.5rem;
  background: #e1e1e1;
  color: #a109c2;
  margin: 0 0.5rem;
  border-radius: 50%;
}

.footer-icons i:hover {
  background: #ca99f0;
  color: #fff;
  transition: 0.6s;
  cursor: pointer;
}

.Footer h5 {
  color: #a109c2;
  text-align: center;
}

.ft-2 ul {
  list-style: none;
  padding-left: 0;
}

.ft-2 ul li {
  padding: 0.35rem 0;
  font-weight: 500;
}

.ft-2 ul a {
  text-decoration: none;
  color: #000;
  font-size: 1.06rem;
}

.ft-3 p {
  font-weight: 500;
  padding: 0.1rem 0;
  font-size: 1.06rem;
}

.ft-3 i {
  padding-right: 0.5rem;
}

.Last-footer {
  background: #af53d5;
  text-align: center;
  padding: 1rem 0;
}

.Last-footer p {
  margin-bottom: 0;
  font-size: 1.2rem;
  font-weight: 700;
}

/* Estilos para el contenedor principal */
.containerCol {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 800px;
  margin: auto;
}

/* Estilos para las secciones */
section {
  padding-top: 20px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin: 10px;
}

.titulo_section {
  text-align: center;
}

.titulo_Imgs {
  text-align: center;
}

.logo {
  align-content: center;
}

.destLila {
  color: #a109c2;
}

/* Estilos para las columnas */
.column {
  flex: 1 1 100%;
  padding: 0px;
}

.h2Col {
  align-content: center;
}

.txtParagraph {
  text-align: justify;
}

.titulo_sectionResenia {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.titulo_section_Creditos {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.titulo_sectionHisto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.list-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.list-group-item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.titulo_sectionImgs {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
  filter: brightness(0.5);
}

.space-y-12 {
  margin: 5rem;
}

.mt-6 {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.accordion-button {
  background-color: #ca99f0;
  color: #000;
}

.list-group-Tutoriales {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  align-items: center;
}

.list-group-mapa {
  list-style: none;
}

.list-group-item-mapa {
  text-align: center;
  padding-bottom: 30px;
}

.imgFinal {
  padding-top: 30px;
}
